                                    <template slot-scope="scope">
                                        <el-tag :type="Number(scope.row.value) > Number(scope.row.norm.split('~')[1]) ? 'danger' : 'success'" close-transition></el-tag>
                                    </template>
                                </el-table-column>
                                <el-table-column prop="name" label="" align="left" show-overflow-tooltip width="200"></el-table-column>
