                                <el-table-column prop="value" label="" align="left" show-overflow-tooltip width="300">
                                    <template slot-scope="scope">
                                        <el-progress :stroke-width="18" :text-inside="true"
                                            :percentage="Number(scope.row.value) > Number(scope.row.norm.split('~')[1]) ? 100 : Number(((scope.row.value / scope.row.norm.split('~')[1]) * 100).toFixed(0))"
