<template>
  <div class="quarantine-add-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="quarantine-add-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span></span>
            </div>
            <div class="content">
                <el-form :model="quarantineForm" :rules="quarantineRules" ref="quarantineForm" label-width="100px">
                    <el-row type="flex" :gutter="5">
                        <el-col :span="8">
                            <el-form-item label="">
                                <el-input v-model="quarantineForm.inCode" clearable placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="">
                                <el-input v-model="quarantineForm.number" clearable placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="">
                                <el-select v-model="quarantineForm.user" clearable placeholder="">
                                    <el-option label="" value=""></el-option>
                                    <el-option label="" value=""></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <div class="quarantine-add-scroll">
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span></span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-table class="quarantine-add-table" :data="quarantineData01" highlight-current-row :show-header="false">
                                    <el-table-column prop="name" label="" header-align="left" show-overflow-tooltip width="200">
                                        <template slot-scope="scope">
                                            <el-checkbox>{{scope.row.name}}</el-checkbox>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="values" label="" header-align="left" show-overflow-tooltip>
                                        <template slot-scope="scope">
                                            <el-checkbox-group v-model="quarantineForm.shijianValue">
                                                <el-checkbox v-for = "(item, index) in scope.row.values" :key="index" :label="item"></el-checkbox>
                                            </el-checkbox-group>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span></span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-table class="quarantine-add-table" :data="quarantineData02" highlight-current-row :show-header="false">
                                    <el-table-column prop="name" label="" header-align="left" show-overflow-tooltip width="200">
                                        <template slot-scope="scope">
                                            <el-checkbox>{{scope.row.name}}</el-checkbox>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="values" label="" header-align="left" show-overflow-tooltip>
                                        <template slot-scope="scope">
                                            <el-checkbox-group v-model="quarantineForm.shijianValue">
                                                <el-checkbox v-for = "(item, index) in scope.row.values" :key="index" :label="item"></el-checkbox>
                                            </el-checkbox-group>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span></span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-table class="quarantine-add-table" :data="quarantineData03" highlight-current-row :show-header="false">
                                    <el-table-column prop="name" label="" header-align="left" show-overflow-tooltip width="200">
                                        <template slot-scope="scope">
                                            <el-checkbox>{{scope.row.name}}</el-checkbox>
                                        </template>
                                    </el-table-column>
                                    <el-table-column prop="values" label="" header-align="left" show-overflow-tooltip>
                                        <template slot-scope="scope">
                                            <el-checkbox-group v-model="quarantineForm.shijianValue">
                                                <el-checkbox v-for = "(item, index) in scope.row.values" :key="index" :label="item"></el-checkbox>
                                            </el-checkbox-group>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span></span>
                            </div>
                            <div class="quarantine-add-item">
                                <el-form-item label="" class="quarantine-add-remark">
                                    <el-input v-model="quarantineForm.remark" type="textarea" :rows="6" placeholder=""></el-input>
                                </el-form-item>
                            </div>
                        </el-card>
                        <div class="down-but">
                            <i class="md-icon material-icons down">&#xE5DB;</i>
                        </div>
                        <el-form-item label="">
                            <el-radio v-model="quarantineForm.jieguo" label="1"></el-radio>
                            <el-radio v-model="quarantineForm.jieguo" label="0"></el-radio>
                        </el-form-item>
                        <el-form-item v-show="quarantineForm.jieguo == 0" label="">
                            <el-input v-model="quarantineForm.yuanyin" type="textarea" :rows="6" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="quarantineForm.remark" type="textarea" :rows="6" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-upload action=""
                                :on-preview="handlePreview"
                                :on-remove="handleRemove"
                                :before-remove="beforeRemove"
                                multiple
                                :limit="3"
                                :on-exceed="handleExceed"
                                :file-list="fileList">
                                <el-button size="small" type="primary"></el-button>
                                <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                            </el-upload>
                        </el-form-item>
                        <div slot="footer" class="bottom-footer">
                            <el-button size="small" class="btn-common btn-back" @click="handleReset()"></el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="handleSave()"></el-button>
                        </div>
                    </div>
                </el-form>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./quarantine_service.js";
export default {
  data () {
      return {
          quarantineForm: {
              shijianValue: [],
              jieguo: '1',
          },
          fileList: [],
          quarantineRules: {},
          quarantineData01: [{
              name: '',
              values: ['','','','']
          }, {
              name: '',
              values: ['','','']
          }, {
              name: '',
              values: ['', '', '']
          },{
              name: '',
              values: ['']
          }],
          quarantineData02: [{
              name: '',
              values: ['', '']
          }, {
              name: '',
              values: ['','']
          }, {
              name: '',
              values: ['', '', '']
          }, {
              name: '',
              values: ['', '', '']
          }, {
              name: '',
              values: ['', '']
          }, {
              name: '',
              values: ['', '', '']
          }],
          quarantineData03: [{
              name: '',
              values: ['']
          }, {
              name: '',
              values: ['','']
          }, {
              name: '',
              values: ['']
          }, {
              name: '',
              values: ['', '', '']
          }, {
              name: '',
              values: ['']
          }]
      }
  },
  mounted () {
    /**  */
    $(".quarantine-add-scroll").lzscroll("y", 60, "hover", "mytheme");
  },
  created () {
      
  },
  watch: {
      
  },
  methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },
      handleReset() {},
      handleSave() {},
  }
}
</script>

<style lang="sass">
@import './add_quarantine.scss'
</style>



