<template>
  <div class="login-page">
      <div class="login-form">
          <div class="login-title">
              <img src="../../assets/images/main/favicon-defalut.png">
              <span></span>
          </div>
          <el-form :model="userForm" :rules="userRules" ref="userForm">
            <el-form-item label="" prop="user">
                <el-input v-model="userForm.user" clearable placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="pwd">
                <el-input type="password" v-model="userForm.pwd" clearable placeholder=""></el-input>
            </el-form-item>
          </el-form>
          <div class="bottom-btn">
              <el-button type="primary" round @click="hanleLogin"></el-button>
              <el-checkbox v-model="jizhumima"></el-checkbox>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userForm: {
        user: "",
        pwd: ""
      },
      userRules: {
        user: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 6, message: " 3  6 ", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 18, message: " 6  18 ", trigger: "blur" }
        ]
      },
      jizhumima: true
    };
  },

  mounted() {
    let that = this;
    $(".login-form").bind("keydown", function(e) {
      // FFIEOpera
      var theEvent = e || window.event;
      var code = theEvent.keyCode || theEvent.which || theEvent.charCode;
      if (code == 13) {
        that.hanleLogin();
      }
    });
  },

  methods: {
    hanleLogin() {
      this.$refs["userForm"].validate(valid => {
        if (valid) {
          if (this.userForm.user != "cntzc" && this.userForm.user != "admin") {
            this.$popup.showErrorNoti({ message: "" });
          } else {
            let userName = this.userForm.user == "cntzc" ? "" : "";
            lscache.set("user", this.userForm.user, constants.SESSION_LIMIT); // 
            lscache.set("userName", userName, constants.SESSION_LIMIT); // 
            let path = this.userForm.user == "cntzc" ? "/HomePage" : "/ManageHome";
            this.$router.push(path);
          }
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="sass">
@import './login.scss';
</style>


