import Vue from 'vue'
import Router from 'vue-router'

import routes from "./routes.js";

Vue.use(Router)

const router = new Router({
    // HTML5 historynginx
    // mode: 'history',
    routes
  })
  
  // 
  router.beforeEach((to, from, next) => {
    // console.log('' + to.path + '--' + from.path)
  
    next()
  })
  
  // // 
  // router.afterEach(transition => {
  
  // })


export default router



