// 
import mapConfig from './config/';
// 
import mapOper from './oper/mapOper';
// 
import layerOper from './oper/layerOper';
// 
import measureOper from './oper/measureOper';
// 
import queryOper from './oper/queryOper';
// 
import interactionOper from './oper/interactionOper';
// 
import featureOper from './oper/featureOper';
// 
import markerOper from './oper/markerOper';
// 
import drawOper from './oper/drawOper';

class mapApi {

  init() {
    // 
    this.mapOper = new mapOper(mapConfig);
    // 
    this.mapOper.initMap();

    this.map = this.mapOper.map;

    // 
    this.layerApi = new layerOper(this.mapOper.map);
    // 
    this.measureApi = new measureOper(this.mapOper.map);
    // 
    this.queryApi = new queryOper(this.mapOper.map);
    // 
    this.interactionApi = new interactionOper(this.mapOper.map);
    // 
    this.featureApi = new featureOper(this.mapOper.map);
    // 
    this.markerApi = new markerOper(this.mapOper.map);
    // 
    this.drawApi = new drawOper(this.mapOper.map);

    return this.mapOper;
  }

  /**
   * 
   */
  setStateToPan() {
    // 
    this.deactiveDraw();
    this.deactiveMeasure();
  }

  /**
   * 
   */
  clear(){
    // 
    this.deactiveDraw();
    this.deactiveMeasure();
    
    // 
    let layer = this.layerApi.getLayerByName('');
    layer.getSource().clear(true);
  }

  /** START  */

  /**
   * 
   */
  activeMeasureLength() {
    let layer = this.layerApi.getLayerByName('');

    this.measureApi.activeMeasureLength(layer.getSource());

  }

  /**
   * 
   */
  activeMeasureArea() {
    let layer = this.layerApi.getLayerByName('');

    this.measureApi.activeMeasureArea(layer.getSource());
  }

  // 
  deactiveMeasure() {
    this.measureApi.deactiveMeasure();
  }

  /** END  */

  /** START  */

  /**
   * 
   */
  activeDraw(params = {}) {
    let layer = this.layerApi.getLayerByName('');

    params.source = layer.getSource();

    this.drawApi.activeDraw(params);
  }

  /**
   * 
   */
  deactiveDraw() {
    this.drawApi.deactiveDraw();
  }

  /** END  */

  /** START  */
  addSelectAction(params) {
    let layers = [];
    if (params.layers) {
      for (let i = 0; i < params.layers.length; i++) {
        layers.push(this.layerApi.getLayerByName(params.layers[i]));
      }
    }

    params.layers = layers;

    this.interactionApi.addSelectAction(params);
  }
  /** END  */
}

export default new mapApi();


