<template>
  <div class="manage-home-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="manage-home-content uk-flex uk-flex-column uk-flex-nowrap">
            <el-row class="top-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="one-card-item" v-for="(itemMenu, indexMenu) in outInData" :key="indexMenu">
                        <div class="in-out-title">
                            <span class="title">{{ indexMenu == 0 ? '' : '' }}</span>
                            <div class="in-out-slick">
                                <div class="slick-item" v-for="(item, index) in itemMenu" :key="index">
                                    <img :src="item.imgSrc">
                                    <span :title="' ' + item.total + ' '">{{ item.type }}  {{ item.total }} </span>
                                    <span></span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                </div>
                            </div>
                        </div>
                        <div class="one-card-content">
                            <div class="one-content-item" v-for="(item, index) in itemMenu" :key="index">
                                <div class="title">{{ item.type }}</div>
                                <div class="mid">
                                    <img :src="item.imgSrc">
                                    <span :title="' ' + item.total + ' '">{{ item.type }}  {{ item.total }} </span>
                                </div>
                                <div class="bottom">
                                    <span></span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                    <span>{{ item.type }} {{item.number}} </span>
                                </div>
                            </div>
                        </div>
                      </div>
                  </div>
              </el-col>
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title"></div>
                      <div class="two-btn">
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-date-picker v-model="inOutDate" type="daterange" range-separator="-" start-placeholder="" end-placeholder="" @change="handleCheckDate"></el-date-picker>
                      </div>
                      <div class="in-out-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="8">
                  <div class="card">
                      <div class="title"></div>
                      <div class="three-btn">
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                      </div>
                      <div class="pie-echarts"></div>
                  </div>
              </el-col>
            </el-row>
            <el-row class="bittom-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title"></div>
                      <div class="slaughte-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="16">
                  <div class="card">
                      <div class="title"></div>
                      <div class="slaughte-map">
                        <vMap ref="map"></vMap>
                      </div>
                  </div>
              </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
/**  */
import vMap from "../../components/map/MapComponent.vue";
// 
import Slick from "vue-slick";

export default {
  components: {
    vMap,
    Slick
  },
  data() {
    return {
      lineCharts: null,
      pieCharts: null,
      slaughteCharts: null,
      mapApi: null,
      outInData: [
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "",
            total: "25420.8",
            trend: "el-icon-caret-top",
            number: "104",
            percent: "12.5"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "",
            total: "1250.3",
            trend: "el-icon-caret-bottom",
            number: "56",
            percent: "5.9"
          },
          {
            imgSrc: require("../../assets/images/home/yang.png"),
            type: "",
            total: "8652.9",
            trend: "el-icon-caret-top",
            number: "238",
            percent: "15.9"
          }
        ],
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "",
            total: "21001.3",
            trend: "el-icon-caret-top",
            number: "86",
            percent: "9.4"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "",
            total: "19502.5",
            trend: "el-icon-caret-bottom",
            number: "78",
            percent: "3.1"
          }
        ]
      ],
      /**  */
      lineOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["", ""],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "",
          boundaryGap: true,
          data: ["", "", "", "", "", "", ""],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          },
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "",
            data: [20, 32, 51, 34, 29, 33, 32],
            type: "line",
            smooth: true,
            areaStyle: {
                color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                        offset: 0, 
                        color: '#0EB206'
                    }, {
                        offset: 1, 
                        color: '#FEF6F3'
                    }],
                    globalCoord: false
                }
            },
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "",
            data: [24, 35, 55, 45, 32, 44, 42],
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            areaStyle: {
                color: {
                    type: 'linear',
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [{
                        offset: 0, 
                        color: '#f72020'
                    }, {
                        offset: 1, 
                        color: '#FEF6F3'
                    }],
                    globalCoord: false
                }
            },
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      inOutDate: "",
      /**  */
      pieOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["", "", ""]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["40%", "55%"],
            selectedMode: 'single',
            label: {
              normal: {
                show: false,
                formatter: "{a|{a}}{abg|}\n{hr|}\n  {b|{b}}{c}  {per|{d}%}",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                shadowBlur: 3,
                rich: {
                  a: {
                    color: "#999",
                    lineHeight: 22,
                    align: "center"
                  },
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 12034, name: "", selected: true },
              { value: 8350, name: "" },
              { value: 9010, name: "" },
            ]
          }
        ]
      },
      /**  */
      slaughteOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["", "", "", "", ""]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            avoidLabelOverlap: false,
            selectedMode: 'single',
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 15, name: "", selected: true },
              { value: 31, name: "" },
              { value: 13, name: "" },
              { value: 9, name: "" },
              { value: 5, name: "" }
            ]
          }
        ]
      },
      slaughteListData: [
        {
          id: "1",
          name: "",
          lon: "108.936779",
          lat: "34.312734",
          type: "redHot"
        },
        {
          id: "2",
          name: "",
          lon: "108.956779",
          lat: "34.342734",
          type: "redHot"
        },
        {
          id: "3",
          name: "",
          lon: "108.996779",
          lat: "34.322734",
          type: "redHot"
        },
        {
          id: "4",
          name: "",
          lon: "108.956779",
          lat: "34.272734",
          type: "redHot"
        },
      ]
    };
  },
  mounted() {
    /**  */
    $(".one-card-content").lzscroll("y", 60, "hover", "mytheme");

    /**  */
    let lineDom = $(".in-out-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);

    /**  */
    let pieDom = $(".pie-echarts")[0];
    this.pieCharts = this.$echarts.init(pieDom);
    this.pieCharts.setOption(this.pieOptions);

    /**  */
    let slaughtePieDom = $(".slaughte-echarts")[0];
    this.slaughteCharts = this.$echarts.init(slaughtePieDom);
    this.slaughteCharts.setOption(this.slaughteOptions);

    // 
    this.mapApi = this.$refs["map"].mapApi;

    $(".in-out-slick").slick({
      slidesToShow: 1,
      autoplaySpeed: '1000',
      arrows: false,
      vertical: true,
      autoplay: true,
      infinite: true
    });
  },
  created() {
    this.$nextTick(() => {
      this.mapApi.markerApi.removeMarkerByType("redHot");
      /**  */
      this.mapApi.markerApi.addMarkers(this.slaughteListData);
    });
  },
  watch: {},
  methods: {
    /**  */
    handleCheckDate() {}
  }
};
</script>

<style lang="sass">
@import './manage_home.scss'

@import '../../../node_modules/slick-carousel/slick/slick.css';
</style>



