<template>
  <div class="slaughte-census-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="slaughte-census-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE01D;</i>
                </span>
                <span></span>
            </div>
            <div class="content">
                <el-form :model="slaughteForm" :rules="slaughteRules" ref="slaughteForm" label-width="120px">
                  <el-row>
                    <el-col :span="8">
                        <el-form-item label="">
                            <el-input v-model="slaughteForm.name" clearable placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="">
                            <el-select v-model="slaughteForm.type" clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="">
                            <el-date-picker v-model="slaughteForm.date" type="daterange" range-separator="-" start-placeholder="" end-placeholder="" @change="handleCheckDate"></el-date-picker>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
                <div slot="footer" class="bottom-footer">
                    <el-button size="mini" class="btn-common btn-back" @click="handleReset()"></el-button>
                    <el-button size="mini" class="btn-common btn-query" @click="handleCensus()"></el-button>
                </div>
                <div class="census-echarts">
                    <el-row type="flex" :gutter="10">
                      <el-col :span="12">
                          <el-card class="box-card">
                            <div class="pie-echarts"></div>
                          </el-card>
                      </el-col>
                      <el-col :span="12">
                          <el-card class="box-card">
                            <div class="slaughte-echarts"></div>
                          </el-card>
                      </el-col>
                    </el-row>
                    <div class="line-charts">
                        <el-card class="box-card">
                            <div class="in-out-echarts"></div>
                        </el-card>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
export default {
  data() {
    return {
      slaughteForm: {
        name: "",
        type: ""
      },
      slaughteRules: {},
      pieCharts: null,
      lineCharts: null,
      slaughteCharts: null,
      /**  */
      lineOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["", ""],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "",
          boundaryGap: true,
          data: ["", "", "", "", "", "", ""],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          },
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "",
            data: [20, 32, 91, 34, 29, 33, 32],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "",
            data: [24, 28, 45, 95, 24, 68, 56],
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      /**  */
      pieOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["", "", ""]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["40%", "55%"],
            selectedMode: 'single',
            label: {
              normal: {
                show: false,
                formatter: "{a|{a}}{abg|}\n{hr|}\n  {b|{b}}{c}  {per|{d}%}  ",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                shadowBlur: 3,
                rich: {
                  a: {
                    color: "#999",
                    lineHeight: 22,
                    align: "center"
                  },
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 12034, name: "", selected: true },
              { value: 8350, name: "" },
              { value: 9010, name: "" },
            ]
          }
        ]
      },
      /**  */
      slaughteOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["", "", "", "", ""]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            selectedMode: 'single',
            avoidLabelOverlap: false,
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "600"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              },
            },
            data: [
              { value: 15, name: "", selected: true},
              { value: 31, name: "" },
              { value: 13, name: "" },
              { value: 9, name: "" },
              { value: 5, name: "" }
            ]
          }
        ]
      }
    };
  },
  mounted() {
    /**  */
    let lineDom = $(".in-out-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);

    /**  */
    let pieDom = $(".pie-echarts")[0];
    this.pieCharts = this.$echarts.init(pieDom);
    this.pieCharts.setOption(this.pieOptions);

    /**  */
    let slaughtePieDom = $(".slaughte-echarts")[0];
    this.slaughteCharts = this.$echarts.init(slaughtePieDom);
    this.slaughteCharts.setOption(this.slaughteOptions);
  },
  created() {},
  watch: {},
  methods: {
    handleCheckDate() {},

    /**  */
    handleCensus() {},

    /**  */
    handleReset() {}
  }
};
</script>

<style lang="sass">
@import './slaughte_census.scss'
</style>



