                            <el-col :span="12">
                                <el-form-item label="">
                                    <el-input v-model="inForm.inCode" clearable placeholder=""></el-input>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-select v-model="inForm.type" clearable placeholder="">
