                            </el-col>
                            </el-row>
                            <el-form-item label="">
                                <el-input v-model="inForm.remark" type="textarea" :rows="3" clearable placeholder=""></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="bottom-footer">
                            <el-button size="small" class="btn-common btn-back" @click="handleInReset()"></el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="handleInSave()"></el-button>
                        </div>
                    </div>
                  </el-card>
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
