                                        <el-option label="" value=""></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="()">
                                    <el-input v-model="outForm.weight" clearable placeholder=""></el-input>
                                </el-form-item>
