                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="bottom-footer">
                            <el-button size="small" class="btn-common btn-back" @click="handleOutReset()"></el-button>
                            <el-button size="small" class="btn-common btn-ok" @click="handleOutSave()"></el-button>
                        </div>
                    </div>
                  </el-card>
                  
              </el-col>
              <el-col :span="6">
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span></span>
                    </div>
                    <el-table class="record-table" :data="inRecord" highlight-current-row :show-header="false">
                        <el-table-column prop="pic" label="pic" align="center" show-overflow-tooltip width="50px">
