                        <el-table-column prop="time" label="" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="number" label="()" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-tag type="success" close-transition>
                                    {{ scope.row.number }} 
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total" label="()" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <span style="color: #ff0000">{{ scope.row.total }}</span>
                            </template>
                        </el-table-column>
                    </el-table>
                  </el-card>
                  <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span></span>
                    </div>
                    <el-table class="record-table" :data="outRecord" highlight-current-row :show-header="false">
                        <el-table-column prop="pic" label="pic" align="center" show-overflow-tooltip width="50px">
                            <template slot-scope="scope">
                                <img :src="scope.row.pic">
                            </template>
                        </el-table-column>
