        outRecord: [{
            pic: require('../../assets/images/home/niu.png'),
            time: '2018-5-3',
            type: '',
            total: '5486'
        }]
    };
  },

  mounted() {
    /**  */
    $(".form-scroll").lzscroll("y", 60, "hover", "mytheme");
  },

  created() {},

  watch: {},

  methods: {
      /**  */
      handleInReset() {

      },
      /**  */
      handleInSave () {

      },

      /**  */
      handleInReset() {

      },
      /**  */
      handleInSave () {

      }
  }
};
</script>

<style lang="sass">
@import './outIn_manage.scss'
</style>



