<template>
  <div class="manage-home-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="manage-home-content uk-flex uk-flex-column uk-flex-nowrap">
            <el-row class="top-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="one-card-item" v-for="(itemMenu, indexMenu) in outInData" :key="indexMenu">
                        <div class="in-out-title">
                            <span class="title">{{indexMenu == 0 ? '' : '' }}</span>
                            <div class="in-out-slick">
                                <div class="slick-item" v-for="(item, index) in itemMenu" :key="index">
                                    <img :src="item.imgSrc">
                                    <span :title="' ' + item.total + ' '">{{ item.type }}  {{ item.total }} </span>
                                    <span></span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                </div>
                            </div>
                        </div>
                        <div class="one-card-content">
                            <div class="one-content-item" v-for="(item, index) in itemMenu" :key="index">
                                <div class="title">{{ item.type }}</div>
                                <div class="mid">
                                    <img :src="item.imgSrc">
                                    <span :title="' ' + item.total + ' '">{{ item.type }}  {{ item.total }} </span>
                                </div>
                                <div class="bottom">
                                    <span></span>
                                    <i :class="item.trend"></i>
                                    <span>{{ item.percent }}%</span>
                                    <span>{{ item.type }} {{item.number}} </span>
                                </div>
                            </div>
                        </div>
                      </div>
                  </div>
              </el-col>
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title"></div>
                      <div class="two-btn">
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-date-picker v-model="inOutDate" type="daterange" range-separator="-" start-placeholder="" end-placeholder="" @change="handleCheckDate"></el-date-picker>
                      </div>
                      <div class="in-out-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="8">
                  <div class="card">
                      <div class="title"></div>
                      <div class="three-btn">
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                        <el-button size="mini" type="primary"></el-button>
                      </div>
                      <div class="pie-echarts"></div>
                  </div>
              </el-col>
            </el-row>
            <el-row class="bittom-row">
              <el-col :span="8" class="righr-line">
                  <div class="card">
                      <div class="title"></div>
                      <div class="bar-echarts"></div>
                  </div>
              </el-col>
              <el-col :span="16">
                  <div class="card">
                      <div class="title"></div>
                      <div class="total">
                          <span> <span style="color: #F64D46">2</span> </span>
                          <el-select v-model="userStatus" clearable placeholder="">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                          </el-select>
                      </div>
                      <el-table class="user-table" :data="userData" highlight-current-row height="calc(100% - 109px)">
                        <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                        <el-table-column prop="name" label="" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="location" label="" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="status" label="" align="center" show-overflow-tooltip>
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.status == 1 ? 'success' : 'danger'"></el-tag>
                                {{scope.row.status == 1 ? '' : ''}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="langTime" label="()" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="startTime" label="" align="center" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="endTime" label="" align="center" show-overflow-tooltip></el-table-column>
                      </el-table>
                      <div class="main-page">
                          <el-pagination @size-change="handleSize" @current-change="handleCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                          </el-pagination>
                      </div>
                  </div>
              </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import service from "./home_service.js";
// 
import Slick from "vue-slick";

export default {
  components: {
    Slick
  },
  data() {
    return {
      lineCharts: null,
      pieCharts: null,
      barCharts: null,
      outInData: [
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "",
            total: "25420.8",
            trend: "el-icon-caret-top",
            number: "104",
            percent: "12.5"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "",
            total: "1250.3",
            trend: "el-icon-caret-bottom",
            number: "56",
            percent: "5.9"
          },
          {
            imgSrc: require("../../assets/images/home/yang.png"),
            type: "",
            total: "8652.9",
            trend: "el-icon-caret-top",
            number: "238",
            percent: "15.9"
          }
        ],
        [
          {
            imgSrc: require("../../assets/images/home/zhu.png"),
            type: "",
            total: "21001.3",
            trend: "el-icon-caret-top",
            number: "86",
            percent: "9.4"
          },
          {
            imgSrc: require("../../assets/images/home/niu.png"),
            type: "",
            total: "19502.5",
            trend: "el-icon-caret-bottom",
            number: "78",
            percent: "3.1"
          }
        ]
      ],
      /**  */
      lineOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "axis",
          formatter: "{b}<br/>{a0}: {c0}  <br/> {a1}: {c1}  "
        },
        legend: {
          data: ["", ""],
          textStyle: {
            color: "#383838"
          },
          top: 40
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: {
          type: "category",
          name: "",
          boundaryGap: true,
          data: ["", "", "", "", "", "", ""],
          axisLine: {
            lineStyle: {
              color: "#383838"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          },
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "",
            data: [20, 32, 51, 34, 29, 33, 32],
            type: "line",
            smooth: true,
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "#0EB206"
                  },
                  {
                    offset: 1,
                    color: "#FEF6F3"
                  }
                ],
                globalCoord: false
              }
            },
            itemStyle: {
              normal: {
                color: "#0EB206"
              }
            }
          },
          {
            name: "",
            data: [24, 35, 55, 45, 32, 44, 42],
            type: "line",
            smooth: true,
            yAxisIndex: 1,
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "#f72020"
                  },
                  {
                    offset: 1,
                    color: "#FEF6F3"
                  }
                ],
                globalCoord: false
              }
            },
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      inOutDate: "",
      /**  */
      pieOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c}  ({d}%)"
        },
        legend: {
          orient: "vertical",
          textStyle: {
            color: "#383838"
          },
          top: "middle",
          right: "10%",
          data: ["", "", ""]
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["40%", "55%"],
            selectedMode: "single",
            label: {
              normal: {
                show: false,
                position: "left",
                formatter: "{a|{a}}{abg|}\n{hr|}\n  {b|{b}}{c} {per|{d}%}",
                backgroundColor: "#eee",
                borderColor: "#aaa",
                borderWidth: 1,
                borderRadius: 4,
                shadowBlur: 3,
                rich: {
                  a: {
                    color: "#999",
                    lineHeight: 22,
                    align: "center"
                  },
                  hr: {
                    borderColor: "#aaa",
                    width: "100%",
                    borderWidth: 0.5,
                    height: 0
                  },
                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: "#eee",
                    backgroundColor: "#334455",
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "100%",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 12034, name: "", selected: true },
              { value: 8350, name: "" },
              { value: 9010, name: "" }
            ]
          }
        ]
      },
      /**  */
      barOptions: {
        tooltip: {
          trigger: "axis",
          formatter: values => {
            let title = "";
            let content = "";
            let total = 0;
            _.map(values, item => {
              title = item.name;
              content += "<br/>" + item.seriesName + " : " + item.value + " ";
              total += item.value;
            });
            return title + " : " + total + " " + content;
          }
        },
        legend: {
          data: ["", "", ""]
        },
        grid: {
          bottom: 30,
          left: 30,
          right: 40,
          top: 40
        },
        xAxis: [
          {
            type: "category",
            name: "",
            boundaryGap: true,
            data: [
              "",
              "",
              "",
              "",
              "",
              "",
              ""
            ],
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            barWidth: 25,
            stack: "",
            data: [12, 14, 10, 7, 15, 5, 11],
            smooth: true,
            itemStyle: {
              normal: {
                color: "#4CCB72"
              }
            }
          },
          {
            name: "",
            type: "bar",
            barWidth: 25,
            stack: "",
            data: [1, 0, 0, 1, 0, 0, 0],
            smooth: true,
            itemStyle: {
              normal: {
                color: "#206FE5"
              }
            }
          },
          {
            name: "",
            data: [0, 0, 0, 1, 0, 0, 0],
            type: "bar",
            barWidth: 25,
            stack: "",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#F3637C"
              }
            }
          }
        ]
      },
      userData: [
        {
          name: "",
          location: "",
          type: "",
          langTime: "3",
          status: "1",
          startTime: "2018-5-3",
          endTime: "2018-5-6"
        },
        {
          name: "",
          location: "",
          type: "",
          langTime: "2",
          status: "1",
          startTime: "2018-5-1",
          endTime: "2018-5-3"
        },
        {
          name: "",
          location: "",
          type: "",
          langTime: "",
          status: "0",
          startTime: "",
          endTime: ""
        }
      ],
      userStatus: "",
      /**  */
      pageSize: 15,
      /**  */
      total: 3,
      /**  */
      page: 1
    };
  },
  mounted() {
    /**  */
    $(".one-card-content").lzscroll("y", 60, "hover", "mytheme");

    /**  */
    let lineDom = $(".in-out-echarts")[0];
    this.lineCharts = this.$echarts.init(lineDom);
    this.lineCharts.setOption(this.lineOptions);

    /**  */
    let pieDom = $(".pie-echarts")[0];
    this.pieCharts = this.$echarts.init(pieDom);
    this.pieCharts.setOption(this.pieOptions);

    /**  */
    let barDom = $(".bar-echarts")[0];
    this.barCharts = this.$echarts.init(barDom);
    this.barCharts.setOption(this.barOptions);

    $(".in-out-slick").slick({
      slidesToShow: 1,
      autoplaySpeed: '1000',
      arrows: false,
      vertical: true,
      autoplay: true,
      infinite: true
    });
  },
  created() {},
  watch: {},
  methods: {
    /**  */
    handleCheckDate() {},

    /**  */
    handleSize(val) {
      this.userData = [];
      this.pageSize = val;
      this.userDataQuery(1);
    },

    /**  */
    handleCurrent(val) {
      this.userData = [];
      this.page = val;
      this.userDataQuery();
    },

    /**  */
    userDataQuery(pg) {}
  }
};
</script>

<style lang="sass">
@import './home_page.scss';

@import '../../../node_modules/slick-carousel/slick/slick.css';
</style>



