/**
 * @desc 
 * @author Originaljoy
 */

import menus from '../config/menu_config.js'
import popupOper from '../common/popupOper.js'
import ajaxOper from '../ajax/ajaxOper.js'

/**
 * 
 */
const getUserInfo = (params) => {
  ajaxOper.ajaxHttp({
    type: 'get',
    url: constants.SERVER_URL + '/permission/user/currentAll',
    successFunc: (data) => {
      const userInfo = data.data;

      if (!userInfo) {
        popupOper.showModalAlert({
          message: "!",
          sureFunc: () => {
            // 
            userLogout();
          }
        })

        return;
      }
      lscache.set('userInfo', userInfo, constants.SESSION_LIMIT);
      if (lscache.get('userId') == null) {
        lscache.set('userId', userInfo.id, constants.SESSION_LIMIT); // 
        lscache.set('userAccount', userInfo.account, constants.SESSION_LIMIT); // 
        lscache.set('userName', userInfo.userName, constants.SESSION_LIMIT); // 
        lscache.set('roles', userInfo.roleList, constants.SESSION_LIMIT); // 
        lscache.set('depts', userInfo.deptList, constants.SESSION_LIMIT); // 

        // 
        if (_.find(userInfo.roleList, (item) => {
            return item.type == constants.ROLE_SYS_ADMIN
          })) {
          lscache.set('isSysRole', true);
        } else if (
          _.find(userInfo.roleList, (item) => {
            return item.type == constants.ROLE_DEPT_ADMIN
          })
        ) {
          lscache.set('isDeptRole', true);
        }
      }

      params.successFunc && params.successFunc(userInfo);
    },
    errorFunc: (data) => {
      // 
      popupOper.showModalAlert({
        message: "!",
        sureFunc: () => {
          // 
          userLogout();
        }
      })
    }
  })
}

/**
 * 
 * @param userId  
 * @param successFunc 
 */
const getPermission = (params) => {
  ajaxOper.ajaxHttp({
    type: 'get',
    url: constants.SERVER_URL + '/permission/permission/listByUserId?type=1&userId=' + params.userId,
    successFunc: (data) => {

      let rootMenu = _.find(data.data, ['parentPerId', 0]);

      let pmsSystem = _.filter(data.data, item => {
        return item.parentPerId == rootMenu.id;
      })

      // 
      let pmsOne = _.filter(data.data, (item) => {
        return _.find(pmsSystem, ['id', item.parentPerId]);
      })
      // 
      let pmsTwo = _.filter(data.data, (item) => {
        return _.map(pmsOne, 'id').indexOf(item.parentPerId) >= 0;
      })

      const pmsInfo = _.union(pmsOne, pmsTwo);

      // 
      lscache.set('pmsOne', pmsOne);
      lscache.set('pmsTwo', pmsTwo);
      lscache.set('permissions', pmsInfo);

      params.successFunc && params.successFunc(pmsInfo);
    },
    errorFunc: (data) => {
      // 
      // 
      userLogout();
    }
  })
}

/**
 * 
 */
const userLogout = () => {
  // 
  lscache.flush();
  // 
  ajaxOper.ajaxHttp({
    type: "get",
    url: constants.CAS_LOGOUT_URL +
      "?service=" +
      window.location.origin +
      "/logout",
    successFunc: data => {
      window.location.href = window.location.origin;
    }
  });
}

const getDefaultMenu = () => {
  return menus;
}

export default {
  defaultMenus: menus,
  getDefaultMenu,
  getUserInfo,
  getPermission,
  userLogout,
}


