<template>
  <div class="device-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="device-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span></span>
                <span class="device-add">
                    <i class="md-icon material-icons" title="" @click="handleSaveDevice">&#xE145;</i>
                </span>
            </div>
            <div class="content">
                <div class="demo-input-suffix">
                    <span></span>
                    <el-select v-model="deviceType" clearable placeholder="">
                        <el-option label="" value=""></el-option>
                    </el-select>
                </div>
                <el-table class="device-table" :data="deviceData" highlight-current-row height="calc(100% - 79px)">
                    <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                    <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="xinghao" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="name" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="code" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="changjia" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="user" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="phone" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="" align="center" width="120">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleUpdateDevice(scope.$index, scope.row)"></el-button>
                            <el-button type="danger" size="mini" @click="handleDeleteDevice(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="main-page">
                    <el-pagination @size-change="handleDeviceSizeChange" @current-change="handleDeviceCurrentChange" :page-sizes="[15, 30, 45, 60]" :page-size="devicePageSize" layout="total, sizes, prev, pager, next, jumper" :total="deviceTotal">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      deviceType: "",
      deviceData: [
        {
          type: "",
          xinghao: "",
          name: "A34323",
          code: "0012054",
          changjia: "",
          user: "",
          phone: "13259864587"
        },
        {
          type: "",
          xinghao: "",
          name: "E34523",
          code: "0152354",
          changjia: "",
          user: "",
          phone: "13259864587"
        },
        {
          type: "",
          xinghao: "",
          name: "B76234",
          code: "1485234",
          changjia: "",
          user: "",
          phone: "13259864587"
        }
      ],
      /**  */
      devicePageSize: 15,
      /**  */
      deviceTotal: 3,
      /**  */
      devicePage: 1
    };
  },

  mounted() {},

  created() {},

  watch: {},

  methods: {
    /**  */
    handleSaveDevice() {},
    /**  */
    handleUpdateDevice(index, row) {},
    /**  */
    handleDeleteDevice(index, row) {},

    /**  */
    handleDeviceSizeChange(val) {
      this.deviceData = [];
      this.devicePageSize = val;
      this.deviceDataQuery(1);
    },

    /**  */
    handleDeviceCurrentChange(val) {
      this.deviceData = [];
      this.devicePage = val;
      this.deviceDataQuery();
    },

    /**  */
    deviceDataQuery(pg) {
      let param = {
        page: pg || this.devicePage,
        pageSize: this.devicePageSize,
        name: this.clericalName
      };
    }
  }
};
</script>

<style lang="sass">
@import './device_manage.scss'
</style>



