<template>
    <div class="video-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="video-left uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span></span>
            </div>
            <div class="video-left-content">
                <happy-scroll class="video-form" color="rgba(73, 163, 225,0.2)" hide-horizontal resize size="4">
                    <el-form :model="videoForm" :rules="videoRules" ref="videoForm" label-width="100px">
                        <el-form-item label="" prop="videoName">
                            <el-input v-model="videoForm.videoName" clearable placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="videoNumber">
                            <el-input v-model="videoForm.videoNumber" clearable placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="type">
                            <el-select v-model="videoForm.type" clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="baseDescribeInfo">
                            <el-input v-model="videoForm.baseDescribeInfo" type="textarea" :rows="6" clearable placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="locationDescribeInfo">
                            <el-input v-model="videoForm.locationDescribeInfo" type="textarea" :rows="6" clearable placeholder=""></el-input>
                        </el-form-item>
                    </el-form>
                </happy-scroll>
                <div slot="footer" class="bottom-footer">
                    <el-button size="small" class="btn-common btn-back" @click="handleReset()"></el-button>
                    <el-button size="small" class="btn-common btn-ok" @click="handleSave()"></el-button>
                </div>
            </div>
        </div>
        <div class="uk-flex-item-none" style="width:1000px;">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span></span>
            </div>
            <div class="video-right-content">
                <div class="demo-input-suffix">
                    <span></span>
                    <el-select v-model="type" placeholder="" @change="videoListQuery">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </div>
                <el-table class="video-table" :data="videoList" highlight-current-row>
                    <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                    <el-table-column prop="videoName" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="videoNumber" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="baseDescribeInfo" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="locationDescribeInfo" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="" align="center" width="120">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
                            <el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      /**  */
      videoList: [{
          videoName: '1',
          videoNumber: '001',
          type: '',
          baseDescribeInfo: '',
          locationDescribeInfo: 'xxxxxx50'
      }],
      /**  */
      videoForm: {},
      videoRules: {},

      type: ""
    };
  },

  created() {},

  mounted() {},

  methods: {
    /**  */
    handleReset() {},
    /**  */
    handleSave() {},

    /**  */
    handleEdit(index, row) {},

    /**  */
    videoListQuery() {},

    /**  */
    handleDelete(index, row) {}
  }
};
</script>

<style lang="sass">
@import './video_manage.scss'
</style>




