                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span></span>
            </div>
            <div class="content">
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="" name="first">
                        <div class="client-content">
                            <el-form :model="supplierForm" :rules="supplierRules" ref="supplierForm" label-width="120px">
                                <el-row type="flex" :gutter="10">
                                <el-col :span="8">
                                    <el-form-item label="" prop="userName">
                                        <el-input v-model="supplierForm.userName" clearable placeholder=""></el-input>
                                    </el-form-item>
                                    <el-form-item class="half-input" label="">
                                        <el-input v-model="supplierForm.xishuMin" clearable placeholder=""></el-input> -
                                        <el-input v-model="supplierForm.xishuMax" clearable placeholder=""></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="" prop="jingli">
                                        <el-select v-model="supplierForm.jingli" clearable placeholder="">
                                            <el-option label="" value=""></el-option>
                                        </el-select>
                                    </el-form-item>
                                    <el-form-item class="half-input" label="()">
                                        <el-input v-model="supplierForm.cunlanMin" clearable placeholder=""></el-input> -
                                        <el-input v-model="supplierForm.cunlanMax" clearable placeholder=""></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="" prop="type">
                                        <el-select v-model="supplierForm.type" clearable placeholder="">
                                            <el-option label="" value=""></el-option>
                                            <el-option label="" value=""></el-option>
                                            <el-option label="" value=""></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                </el-row>
                            </el-form>
                            <div slot="footer" class="bottom-footer">
                                <el-button size="small" class="btn-common btn-back" @click="handleReset()"></el-button>
                                <el-button size="small" class="btn-common btn-ok" @click="handleQuery()"></el-button>
                            </div>
                            <el-row class="bottom-content" :gutter="10">
                              <el-col :span="18">
                                <el-table class="client-table" :data="supplierData" highlight-current-row>
                                    <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                                    <el-table-column prop="name" label="" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="xishu" label="" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="number" label="()" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column prop="username" label="" align="center" show-overflow-tooltip></el-table-column>
                                    <el-table-column label="" align="center" width="120">
                                        <template slot-scope="scope">
                                            <el-button type="primary" size="mini" @click="handleUpdateDevice(scope.$index, scope.row)"></el-button>
                                            <el-button type="danger" size="mini" @click="handleDeleteDevice(scope.$index, scope.row)"></el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                              </el-col>
                              <el-col :span="6">
                                  <div class="shopping-record">
                                      <div class="shop-title"></div>
                                      <div class="shop-conent">
                                        <el-table class="record-table" :data="supplierRecord" highlight-current-row :show-header="false">
                                            <el-table-column prop="time" label="" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="number" label="()" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.number }} 
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="total" label="()" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.total }} 
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                      </div>
                                  </div>
                              </el-col>
                            </el-row>
                        </div>
