                                <el-col :span="8">
                                    <el-form-item label="" prop="type">
                                        <el-select v-model="saleForm.type" clearable placeholder="">
                                            <el-option label="" value=""></el-option>
                                            <el-option label="" value=""></el-option>
                                            <el-option label="" value=""></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                </el-row>
                            </el-form>
