                                <el-button size="small" class="btn-common btn-back" @click="handleReset()"></el-button>
                                <el-button size="small" class="btn-common btn-ok" @click="handleQuery()"></el-button>
                            </div>
                            <el-row class="bottom-content" :gutter="10">
                              <el-col :span="18">
                                <el-table class="client-table" :data="saleData" highlight-current-row>
