                                            <el-button type="danger" size="mini" @click="handleDeleteDevice(scope.$index, scope.row)"></el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                              </el-col>
                              <el-col :span="6">
                                  <div class="shopping-record">
                                      <div class="shop-title"></div>
                                      <div class="shop-conent">
                                        <el-table class="record-table" :data="saleRecord" highlight-current-row :show-header="false">
                                            <el-table-column prop="time" label="" align="center" show-overflow-tooltip></el-table-column>
                                            <el-table-column prop="number" label="()" align="center" show-overflow-tooltip>
                                                <template slot-scope="scope">
                                                    {{ scope.row.number }} 
