</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
        activeName: 'first',
        supplierForm: {

        },
        supplierRules: {

        },

        supplierData: [{
            name: '',
            type: '',
            xishu: '1.5',
            number: '2513',
            username: ''
        }],

        supplierRecord: [{
            time: '2018-2-4',
            number: '251',
            type: '',
            total: '3250'
        }],

        saleForm: {

        },
        saleRules: {

        },
        saleData: [{
            name: '',
            type: '',
            number: '2513',
            username: ''
        }],

        saleRecord: [{
            time: '2018-2-4',
            number: '251',
            type: '',
            total: '3250'
        }],

    };
  },

  mounted() {},

  created() {},

  watch: {},

  methods: {
      handleClick() {},

      /**  */
      handleReset() {},

      /**  */
      handleQuery() {},
  }
};
</script>

<style lang="sass">
@import './client_manage.scss'
</style>



