/**
 * @desc 
 * @author Originaljoy
 */

/**
 * 
 * @param {string} message
 * @param {string} type
 * @param {string} trigger
 */
const valiRequired = (message, trigger, type) => {
  return {
    required: true,
    message: message,
    trigger: trigger ? trigger : 'blur',
    type: type ? type : 'string'
  }
}

/**
 * 
 * @param {double} max 
 */
const valiMaxLength = (max, trigger) => {
  return {
    max: max,
    validator: vLength,
    trigger: trigger ? trigger : 'blur'
  }
}

/**
 *  Validator
 * Js
 *      
 *      
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 */
const vLength = (rule, value, callback) => {
  if (value == undefined) {
    callback();
    return;
  }

  if (rule.max && !rule.min) {
    if (uLength(value) > rule.max) {
      callback(new Error('' + rule.max + ' !'));
    } else {
      callback();
    }
  } else if (!rule.max && rule.min) {
    if (uLength(value) < rule.min) {
      callback(new Error('' + rule.min + ' !'));
    } else {
      callback();
    }
  } else if (rule.max && rule.min) {
    if (uLength(value) < rule.min && uLength(value) > rule.max) {
      callback(new Error('' + rule.min + '' + rule.max + ' !'));
    } else {
      callback();
    }
  }
}

/**
 * 
 * @param {*} max 
 * @param {*} trigger 
 */
const valiTellPhone = (max, trigger) => {
  return {
    validator: vTellPhone,
    trigger: trigger ? trigger : 'blur'
  }
}

/**
 *  Validator
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
const vTellPhone = (rule, value, callback) => {
  if (!value) {
    callback();
    return;
  }

  const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
  if (!reg.test(value)) {
    return callback(new Error(''));
  } else {
    callback();
  }
};

/**
 * 
 * @param {any} text
 */
const uLength = (text) => {
  var len = 0;
  for (var i = 0; i < text.length; i++) {
    var code = text.charCodeAt(i);
    //1
    if ((code >= 0x0001 && code <= 0x007e) || (0xff60 <= code && code <= 0xff9f)) {
      len++;
    } else {
      len += 2;
    }
  }
  return len;
}

// 
const shortcutDateOptions = {
  shortcuts: [{
    text: '',
    onClick(picker) {
      const end = new Date();
      const start = new Date();
      // start.setTime(start.getTime() - 3600 * 1000 * 24);
      picker.$emit('pick', [start, end]);
    }
  }, {
    text: '',
    onClick(picker) {
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      picker.$emit('pick', [start, end]);
    }
  }, {
    text: '',
    onClick(picker) {
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
      picker.$emit('pick', [start, end]);
    }
  }, {
    text: '',
    onClick(picker) {
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
      picker.$emit('pick', [start, end]);
    }
  }]
};

export default {
  valiRequired,
  valiMaxLength,
  valiTellPhone,
  shortcutDateOptions
}


