<template>
  <div class="quarantine-census-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="quarantine-census-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE01D;</i>
                </span>
                <span></span>
            </div>
            <div class="content">
                <el-form :model="slaughteForm" :rules="slaughteRules" ref="slaughteForm" label-width="120px">
                  <el-row>
                    <el-col :span="8">
                        <el-form-item label="">
                            <el-input v-model="slaughteForm.name" clearable placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="">
                            <el-select v-model="slaughteForm.type" clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="">
                            <el-date-picker v-model="slaughteForm.date" type="daterange" range-separator="-" start-placeholder="" end-placeholder="" @change="handleCheckDate"></el-date-picker>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
                <div slot="footer" class="bottom-footer">
                    <el-button size="mini" class="btn-common btn-back" @click="handleReset()"></el-button>
                    <el-button size="mini" class="btn-common btn-query" @click="handleCensus()"></el-button>
                </div>
                <el-row type="flex" :gutter="10" class="mid-charts">
                    <el-col :span="8">
                        <el-card class="box-card">
                            <div class="gauge-echarts"></div>
                        </el-card>
                    </el-col>
                    <el-col :span="16">
                        <el-card class="box-card">
                            <div class="bar-echarts"></div>
                        </el-card>
                    </el-col>
                </el-row>
                <div class="bottom-table">
                    <el-card class="box-card">
                        <div class="title"></div>
                        <el-table class="abnormal-table" :data="abnormalInfoData" highlight-current-row height="calc(100% - 54px)">
                            <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                            <el-table-column prop="name" label="" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="quarantiner" label="" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="disease" label="" align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column prop="level" label="" align="center" show-overflow-tooltip width="120px">
                                <template slot-scope="scope">
                                    <el-tag close-transition :class="scope.row.level == 0 ? 'one-level' : scope.row.level == 1 ? 'two-level' : 'three-level'"></el-tag>
                                    {{ scope.row.level == 0 ? '' : scope.row.level == 1 ? '' : '' }}
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="main-page">
                            <el-pagination @size-change="handleSize" @current-change="handleCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                            </el-pagination>
                        </div>
                    </el-card>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
export default {
  data() {
    return {
      abnormalInfoData: [
        {
          name: "",
          quarantiner: "",
          type: "",
          disease: "",
          level: "0"
        },
        {
          name: "",
          quarantiner: "",
          type: "",
          disease: "",
          level: "1"
        },
        {
          name: "",
          quarantiner: "",
          type: "",
          disease: "",
          level: "2"
        }
      ],
      /**  */
      pageSize: 15,
      /**  */
      total: 3,
      /**  */
      page: 1,
      gaugeCharts: null,
      barCharts: null,
      /**  */
      barOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          bottom: 30,
          left: 50,
          right: 50,
          top: 80
        },
        xAxis: [
          {
            type: "category",
            name: "",
            boundaryGap: true,
            data: [
              "2018-5-1",
              "2018-5-2",
              "2018-5-3",
              "2018-5-4",
              "2018-5-5",
              "2018-5-6",
              "2018-5-7"
            ],
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "",
            axisLabel: {
              formatter: "{value}"
            },
            axisLine: {
              lineStyle: {
                color: "#383838"
              }
            }
          }
        ],
        series: [
          {
            name: "",
            data: [24, 28, 45, 95, 24, 68, 56],
            type: "bar",
            barWidth: "35%",
            smooth: true,
            itemStyle: {
              normal: {
                color: "#f72020"
              }
            }
          }
        ]
      },
      /**  */
      gaugeOptions: {
        title: {
          text: "",
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c}%"
        },
        series: [
          {
            name: "",
            type: "gauge",
            detail: { formatter: "{value}%" },
            data: [{ value: 96.8, name: "" }]
          }
        ]
      },
      slaughteForm: {},
      slaughteRules: {}
    };
  },
  mounted() {
    /**  */
    let barDom = $(".bar-echarts")[0];
    this.barCharts = this.$echarts.init(barDom);
    this.barCharts.setOption(this.barOptions);

    /**  */
    let gaugeDom = $(".gauge-echarts")[0];
    this.gaugeCharts = this.$echarts.init(gaugeDom);
    this.gaugeCharts.setOption(this.gaugeOptions);
  },
  created() {},
  watch: {},
  methods: {
    /**  */
    handleSize(val) {
      this.abnormalInfoData = [];
      this.pageSize = val;
      this.abnormalInfoQuery();
    },

    /**  */
    handleCurrent(val) {
      this.abnormalInfoData = [];
      this.page = val;
      this.abnormalInfoQuery();
    },

    /**  */
    abnormalInfoQuery() {},

    handleCheckDate() {},

    /**  */
    handleCensus() {},

    /**  */
    handleReset() {}
  }
};
</script>

<style lang="sass">
@import './quarantine_census.scss'
</style>



