<template>
    <div class="sewage-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="sewage-left uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE03B;</i>
                </span>
                <span></span>
            </div>
            <div class="sewage-left-content">
                <happy-scroll class="sewage-form" color="rgba(73, 163, 225,0.2)" hide-horizontal resize size="4">
                    <el-form :model="sewageForm" :rules="sewageRules" ref="sewageForm" label-width="100px">
                        <el-form-item label="" prop="sewageName">
                            <el-input v-model="sewageForm.sewageName" clearable placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="sewageNumber">
                            <el-input v-model="sewageForm.sewageNumber" clearable placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="type">
                            <el-select v-model="sewageForm.type" clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="levelInfo">
                            <el-input v-model="sewageForm.levelInfo" type="textarea" :rows="6" clearable placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="locationInfo">
                            <el-input v-model="sewageForm.locationInfo" type="textarea" :rows="6" clearable placeholder=""></el-input>
                        </el-form-item>
                    </el-form>
                </happy-scroll>
                <div slot="footer" class="bottom-footer">
                    <el-button size="small" class="btn-common btn-back" @click="handleReset()"></el-button>
                    <el-button size="small" class="btn-common btn-ok" @click="handleSave()"></el-button>
                </div>
            </div>
        </div>
        <div class="uk-flex-item-none" style="width:1000px;">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span></span>
            </div>
            <div class="sewage-right-content">
                <div class="demo-input-suffix">
                    <span></span>
                    <el-select v-model="type" placeholder="" @change="sewageListQuery">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </div>
                <el-table class="sewage-table" :data="sewageList" highlight-current-row>
                    <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                    <el-table-column prop="sewageName" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="sewageNumber" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="levelInfo" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="locationInfo" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="" align="center" width="120">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
                            <el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import service from "./info_manage.js";

export default {
  data() {
    return {
      /**  */
      sewageList: [
        {
          sewageName: "XXX",
          sewageNumber: "2011251",
          type: "",
          levelInfo: "",
          locationInfo: "xxxxxx99"
        }
      ],
      /**  */
      sewageForm: {},
      sewageRules: {},

      type: ""
    };
  },

  created() {},

  mounted() {},

  methods: {
    /**  */
    handleReset() {},
    /**  */
    handleSave() {},

    /**  */
    handleEdit(index, row) {},

    /**  */
    sewageListQuery() {},

    /**  */
    handleDelete(index, row) {}
  }
};
</script>

<style lang="sass">
@import './sewage_manage.scss'
</style>




