<template>
  <div class="quarantine-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="quarantine-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span></span>
            </div>
            <div class="content">
                <div class="demo-input-suffix">
                    <span></span>
                    <el-select v-model="quarantineUser" clearable placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                    <span style="margin-left: 10px;"></span>
                    <el-radio v-model="isAdopt" label="1"></el-radio>
                    <el-radio v-model="isAdopt" label="0"></el-radio>
                    <el-button size="mini" type="primary" class="btn-query"></el-button>
                </div>
                <el-table class="quarantine-table" :data="quarantineData" highlight-current-row height="calc(100% - 79px)">
                    <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                    <el-table-column prop="time" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="user" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="code" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="result" label="" align="center" show-overflow-tooltip width="100px">
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.result == 1 ? 'success' : 'danger'"></el-tag>
                            {{scope.row.result == 1 ? '' : ''}}
                        </template>
                    </el-table-column>
                </el-table>
                <div class="main-page">
                    <el-pagination @size-change="handleQuarantineSize" @current-change="handleQuarantineCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="quarantineTotal">
                    </el-pagination>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./quarantine_service.js";
export default {
  data() {
    return {
      /**  */
      pageSize: 15,
      /**  */
      quarantineTotal: 2,
      /**  */
      page: 1,
      quarantineUser: "",
      isAdopt: "1",
      quarantineData: [
        {
          time: "2018-5-4",
          user: "",
          code: "I52654258569",
          result: "1"
        },
        {
          time: "2018-5-10",
          user: "",
          code: "I47812352425",
          result: "0"
        }
      ]
    };
  },
  mounted() {},
  created() {},
  watch: {},
  methods: {
    /**  */
    handleQuarantineSize(val) {
      this.quarantineData = [];
      this.pageSize = val;
      this.quarantineDataQuery(1);
    },

    /**  */
    handleQuarantineCurrent(val) {
      this.quarantineData = [];
      this.page = val;
      this.quarantineDataQuery();
    },

    /**  */
    quarantineDataQuery(pg) {}
  }
};
</script>

<style lang="sass">
@import './quarantine_record.scss'
</style>



