/**
 * @desc 
 * @author Originaljoy
 */

import {
  Notification,
  Loading,
  MessageBox
} from 'element-ui';

import validateOper from './validateOper.js';

/**
 * 
 * @param title 
 * @param message 
 * @param type 
 * @param offset 
 * @param duration ,3000
 */
const showNotification = (params) => {
  Notification({
    title: params.title || '',
    message: params.message,
    type: params.type || 'warning',
    offset: params.offset || 120,
    duration: params.duration || 3000
  });
}

/**
 * 
 * @param title 
 * @param message 
 * @param offset 
 * @param duration  
 */
const showSuccessNoti = (params) => {
  params = _.assign(params, {
    type: 'success'
  });

  showNotification(params);
}

/**
 * 
 * @param title 
 * @param message 
 * @param offset 
 * @param duration  
 */
const showErrorNoti = (params) => {
  params = _.assign(params, {
    type: 'error'
  });

  showNotification(params);
}

/**
 * 
 * @param {*} text 
 */
const showLoading = (text) => {
  text = text == undefined ? "..." : text;
  window.mainLoading = Loading.service({
    fullscreen: true,
    text: text,
    customClass: 'page-loading'
  });
}

/**
 * 
 */
const closeLoading = () => {
  setTimeout(function () {
    window.mainLoading && window.mainLoading.close();
  }, 200);
}

/**
 * 
 * @param title 
 * @param message 
 * @param type 
 * @param sureFunc 
 */
const showModalAlert = (params) => {
  MessageBox.alert(params.message, params.title || '', {
    type: params.type || 'info',
    closeOnClickModal: true,
    showConfirmButton: true
  }).then(function () {
    params.sureFunc && params.sureFunc.call();
  });
}

/**
 * 
 * @param title 
 * @param content 
 * @param sureFunc 
 */
const showPrompt = (params) => {
  MessageBox.prompt(params.content, params.title, {
    closeOnClickModal: false,
    confirmButtonText: '',
    cancelButtonText: '',
    inputValidator: [validateOper.valiRequired],
    inputValue: params.inputValue
  }).then(({
    value
  }) => {
    params.sureFunc && params.sureFunc(value);
  }).catch(() => {});
}

/**
 * 
 * @param title 
 * @param message 
 * @param confirmButtonText 
 * @param cancelButtonText 
 * @param successFunc 
 */
const showConfirm = (params) => {
  let title = params.title == undefined ? '' : params.title;
  window.rootVm.$confirm(params.message, title, {
    confirmButtonText: params.confirmButtonText == undefined ? '' : params.confirmButtonText,
    cancelButtonText: params.cancelButtonText == undefined ? '' : params.cancelButtonText,
    type: 'warning'
  }).then(() => {
    params.successFunc && params.successFunc();
  }).catch(() => {
    params.errorFunc && params.errorFunc();
  });
};

export default {
  showNotification,
  showSuccessNoti,
  showErrorNoti,
  showLoading,
  closeLoading,
  showModalAlert,
  showPrompt,
  showConfirm
}


