<template>
  <div class="slaughte-page uk-flex uk-flex-row uk-flex-nowrap">
        <div class="slaughte-content uk-flex uk-flex-column uk-flex-nowrap">
            <div class="mainbar-title">
                <span>
                    <i class="md-icon material-icons">&#xE896;</i>
                </span>
                <span></span>
                <span class="slaughte-add">
                    <i class="md-icon material-icons" title="" @click="handleSaveSlaughte">&#xE145;</i>
                </span>
            </div>
            <div class="content">
                <el-form :model="slaughteForm" :rules="slaughteRules" ref="slaughteForm" label-width="120px">
                  <el-row>
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-input v-model="slaughteForm.name" clearable placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-input class="min" v-model="slaughteForm.min" clearable placeholder=""></el-input>
                            <span>-</span>
                            <el-input class="max" v-model="slaughteForm.max" clearable placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
                <div slot="footer" class="bottom-footer">
                    <el-button size="mini" class="btn-common btn-back" @click="handleReset()"></el-button>
                    <el-button size="mini" class="btn-common btn-query" @click="handleSave()"></el-button>
                </div>
                <el-table class="slaughte-table" :data="slaughteData" highlight-current-row height="calc(100% - 127px)">
                    <el-table-column type="index" label="" align="center" width="50"></el-table-column>
                    <el-table-column prop="name" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="type" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="number" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="total" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="user" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="phone" label="" align="center" show-overflow-tooltip></el-table-column>
                    <el-table-column label="" align="center" width="170">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" @click="handleUpdateSlaughte(scope.$index, scope.row)"></el-button>
                            <el-button class="btn-view" size="mini" @click="handleViewSlaughte(scope.$index, scope.row)"></el-button>
                            <el-button type="danger" size="mini" @click="handleDeleteSlaughte(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="main-page">
                    <el-pagination @size-change="handleSlaughteSize" @current-change="handleSlaughteCurrent" :page-sizes="[15, 30, 45, 60]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
import service from "./manage_service.js";
export default {
  data() {
    return {
      slaughteForm: {
        name: "",
        min: "",
        max: ""
      },
      slaughteRules: {},
      /**  */
      pageSize: 15,
      /**  */
      total: 3,
      /**  */
      page: 1,
      slaughteData: [
        {
          name: "",
          type: "",
          number: "18",
          total: "5500",
          user: "",
          phone: "13258654528"
        },
        {
          name: "",
          type: "",
          number: "21",
          total: "8000",
          user: "",
          phone: "13958462542"
        },
        {
          name: "",
          type: "",
          number: "13",
          total: "3500",
          user: "",
          phone: "13456852459"
        }
      ]
    };
  },
  mounted() {},
  created() {},
  watch: {},
  methods: {
    /**  */
    handleSlaughteSize(val) {
      this.slaughteData = [];
      this.pageSize = val;
      this.slaughteDataQuery();
    },

    /**  */
    handleSlaughteCurrent(val) {
      this.slaughteData = [];
      this.page = val;
      this.slaughteDataQuery();
    },

    /**  */
    slaughteDataQuery() {},

    /**  */
    handleSaveSlaughte(index, row) {},

    /**  */
    handleUpdateSlaughte(index, row) {},

    /**  */
    handleViewSlaughte(index, row) {},

    /**  */
    handleDeleteSlaughte(index, row) {}
  }
};
</script>

<style lang="sass">
@import './slaughte_manage.scss'
</style>



